	module.exports = {"data":{"uiComponentsYaml":{"title":"Tooltip","category":"UI components","examples":[{"title":"Usage","introduction":"The state of the tooltip can be controlled using `isOpen`.\n","subTitle":"Example","react":"import React from 'react';\nimport { Tooltip } from 'reactstrap';\n\nexport default class Example extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.toggle = this.toggle.bind(this);\n    this.state = {\n      tooltipOpen: false\n    };\n  }\n\n  toggle() {\n    this.setState({\n      tooltipOpen: !this.state.tooltipOpen\n    });\n  }\n\n  render() {\n    return (\n      <div>\n        <p>This is an example <a href=\"#\" id=\"TooltipExample\">tooltip</a>.</p>\n        <Tooltip\n          placement=\"bottom\"\n          isOpen={this.state.tooltipOpen}\n          target=\"TooltipExample\"\n          toggle={this.toggle}\n        >\n          Tooltip text bottom\n        </Tooltip>\n      </div>\n    );\n  }\n}\n","css":null,"html":null,"actualHtml":null},{"title":"Uncontrolled variant","introduction":"For simple cases, an uncontrolled tooltip which manages its own state can\nbe used.\n","subTitle":"Example","react":"import React from 'react';\nimport { UncontrolledTooltip } from 'reactstrap';\n\nexport default function Example() {\n  return (\n    <div>\n      <p>This is an example <a href=\"#\" id=\"UncontrolledTooltipExample\">tooltip</a>.</p>\n      <UncontrolledTooltip placement=\"bottom\" target=\"UncontrolledTooltipExample\">\n        Uncontrolled tooltip text\n      </UncontrolledTooltip>\n    </div>\n  );\n}\n","css":null,"html":null,"actualHtml":null},{"title":"Positioning","introduction":"The position of the tooltip can be controlled.\n","subTitle":"Example","react":"import React from 'react';\nimport { UncontrolledTooltip } from 'reactstrap';\n\nexport default function Example() {\n  return (\n    <div>\n      <p>\n        <span id=\"UncontrolledTooltipExampleTop\">top</span>{' '}\n        <UncontrolledTooltip placement=\"top\" target=\"UncontrolledTooltipExampleTop\">\n          Top\n        </UncontrolledTooltip>\n        <span id=\"UncontrolledTooltipExampleRight\">right</span>{' '}\n        <UncontrolledTooltip placement=\"right\" target=\"UncontrolledTooltipExampleRight\">\n          Right\n        </UncontrolledTooltip>\n        <span id=\"UncontrolledTooltipExampleBottom\">bottom</span>{' '}\n        <UncontrolledTooltip placement=\"bottom\" target=\"UncontrolledTooltipExampleBottom\">\n          Bottom\n        </UncontrolledTooltip>\n        <span id=\"UncontrolledTooltipExampleLeft\">left</span>\n        <UncontrolledTooltip placement=\"left\" target=\"UncontrolledTooltipExampleLeft\">\n          Left\n        </UncontrolledTooltip>\n      </p>\n    </div>\n  );\n}\n","css":null,"html":null,"actualHtml":null}],"fields":{"slug":"/ui-components/tooltip/","preHtml":"<p>Tooltips can be attached to any element. When you hover the element with your\nmouse, the title attribute is displayed in a little box next to the element\nthat can be styled. Tooltips are also useful for form elements, to show some\nadditional information in the context of each field and can be style with a\nheading.</p>\n","postHtml":"\n"}}},"pathContext":{"slug":"/ui-components/tooltip/"}}

